import { useRef, useState } from 'react'
import useTheme from '../Hook/useTheme'
import content from '../Localization/Content'
import useLang from '../Hook/useLang'


function Footer(){

    const inputValue = useRef()
    const [disabled, setDisabled] = useState(true)
    const [theme] = useTheme()
    const [lang] = useLang()

    return(<>
        <footer className={`footer ${theme}`}>
            <h1>{content[lang].footer} ({theme})</h1>

            <input onChange={(e) =>{
                if(e.target.value === 'done'){
                    inputValue.current.style.backgroundColor = 'green'
                    setDisabled(false)
                }else{
                    inputValue.current.style.backgroundColor = 'white'
                    setDisabled(true)
                }
            }} ref={inputValue} type="text" placeholder="type"/>
            <button disabled={disabled}>{disabled ? 'done so\'zini kiriting' : 'submit'}</button>
        </footer>
    </>)
}

export default Footer